.row-header{
  margin: 0 auto;
  padding: 0 auto
}

.MainApp{
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

.App{
  flex:1
}

.row-content{
  margin: 0 auto;
  padding: 50px 0 50px 0;
  border-bottom: 1px ridge;
  min-height: 400px;
}

.footer{
  background-color: #d1c4e9;
  margin: 0px auto;
  padding: 20px 0 20px 0
}

.jumbotron{
  padding: 70px 30px 70px 30px;
  margin:0px auto;
  background-color: #9575cd;
  color:floralwhite
}
.address{
  font-size: 80%;
  margin: 0px ;
  color:#0f0f0f
}

body{
  padding:0px 0px 0px 0px;
  z-index:0

}
.navbar-dark{
  background-color: #512da8;
}






.page-enter{
  opacity:0.01;
  transform: translateX(-100%);
}

.page-enter-active {
  opacity: 1;
  transform: translateX(0%);
  transition: all 300ms ease-in;
}

.page-exit{
  opacity: 1;
  transform: translateX(0%);
}

.page-exit-active{
  opacity: 0.01;
  transform: translateX(100%);
  transition: all 300ms ease-out;

}